import React from 'react';
import { connect } from 'react-redux';
import IconButton from '@material-ui/core/IconButton';
import Badge from '@material-ui/core/Badge';
import AccessTime from '@material-ui/icons/AccessTime';
import * as selectors from 'store/selectors';
import * as actions from 'store/actions';

const FarmButton = ({ farmTurns, openFarmTimer }) => (
  <IconButton style={{ outline: 0 }} color="inherit" onClick={openFarmTimer}>
    <Badge
      badgeContent={farmTurns}
      color={farmTurns < 5 ? 'secondary' : 'primary'}
    >
      <AccessTime />
    </Badge>
  </IconButton>
);

export default connect(
  state => ({ farmTurns: selectors.getFarmTurns(state) }),
  actions
)(FarmButton);



