import * as R from 'ramda';
import * as actions from 'store/actions';
import { SNIPE_MODE } from 'types';

const DEFAULT = {
  mode: SNIPE_MODE,
  decoPicker: undefined,
  farmTimer: undefined
};

export default (state = DEFAULT, action) => {
  switch (action.type) {
    case actions.SET_UI_MODE:
      return R.assoc('mode', action.data, state);
    case actions.OPEN_DECO_PICKER:
      return R.assoc('decoPicker', action.data, state);
    case actions.CLOSE_DECO_PICKER:
      return R.assoc('decoPicker', undefined, state);
    case actions.OPEN_FARM_TIMER:
      return R.assoc('farmTimer', true, state);
    case actions.CLOSE_FARM_TIMER:
      return R.assoc('farmTimer', undefined, state);
    case actions.FERTILIZE_FARM:
      return R.assoc('farmTimer', undefined, state);
    case actions.SET_DECO:
      return R.assoc('decoPicker', undefined, state);
    default:
      return state;
  }
};



