import { SET_FARM_TURNS, DO_QUEST, FERTILIZE_FARM } from 'store/actions';

const DEFAULT = 0;

export default (state = DEFAULT, action) => {
  switch (action.type) {
    case SET_FARM_TURNS:
      return action.data;
    case DO_QUEST:
      return Math.max(state - 1, 0);
    case FERTILIZE_FARM:
      return Math.min(state + 5, 9);
    default:
      return state;
  }
};



