import React from 'react';
import { connect } from 'react-redux';
import Dialog from '@material-ui/core/Dialog';
import DialogContent from '@material-ui/core/DialogContent';
import DialogActions from '@material-ui/core/DialogActions';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import * as selectors from 'store/selectors';
import * as actions from 'store/actions';

const FarmTimer = ({
  isOpen,
  closeFarmTimer,
  farmTurns,
  setFarmTurns,
  fertilizeFarm
}) => (
  <Dialog open={isOpen} onClose={closeFarmTimer}>
    <DialogContent>
      <TextField
        id="farm-turns"
        label="Remaining Fertilizer"
        value={farmTurns}
        onChange={event => setFarmTurns(event.target.value)}
        type="number"
      />
    </DialogContent>
    <DialogActions>
      <Button color="primary" variant="contained" onClick={fertilizeFarm}>
        Fertilize
      </Button>
    </DialogActions>
  </Dialog>
);

export default connect(
  state => ({
    isOpen: selectors.isFarmTimerOpen(state),
    farmTurns: selectors.getFarmTurns(state)
  }),
  actions
)(FarmTimer);



